<template>
  <div class="data-row-date">
    <p>Format :</p>
    <select v-model="dataRowItemOption.type" @change="setDataRowItemOption" class="ds-form-select data-row-options">
      <option value="YYYY-MM-DD">YYYY-MM-DD</option>
      <option value="YY-MM-DD">YY-MM-DD</option>
      <option value="YY-M-D">YY-M-D</option>
    </select>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number,
    dataRow: Object,
  },
  setup(props) {
    const store = useStore();
    // default value
    let dataRowItemOption = props.dataRow ? computed(() => props.dataRow.item.itemOption) : ref({ type: 'YYYY-MM-DD' });

    store.commit('setDataRowItemOption', {
      index: props.index,
      dataRowItemOption: dataRowItemOption.value,
    });

    // 선택한 data type을 store에 저장
    const setDataRowItemOption = () => {
      store.commit('setDataRowItemOption', {
        index: props.index,
        dataRowItemOption: dataRowItemOption.value,
      });
      console.log('dataRowItemOption -->', store.getters.getDataRowItemOption(props.index));
    };

    return {
      dataRowItemOption,
      setDataRowItemOption,
    };
  },
});
</script>

